<template>
  <div class="gamers-banner-grid__container">
    <div class="
      gamers-banner-grid__header
    "
    :class="{ 'is-hidden': !section.settings.heading_text && !section.settings.subtitle }"
    >
        <div v-if="section.settings.heading_text" class="h3 gamers-banner-grid__heading" v-text="section.settings.heading_text" />

        <div class="h6 gamers-banner-grid__subtitle" v-text="section.settings.subtitle" />
    </div>

    <div
      class="
        gamers-banner-grid__list
      "
      :class="{'gamers-banner-grid__list--four': section.settings.columns === 4}"
      data-columns="{{ section.settings.columns }}"
      js-gamers-banner-grid="list"
    >
      <template v-for="(hero, index) in heroes" :key="`hero-${index}`">
        <template v-if="hero.linkUrl && hero.imageFile && hero.bannerSub">
          <a
            class="gamers-banner-grid__item"
            :class="grid_item_classes(index + 1)"
            :aria-hidden="ariaHidden(index + 1)"
            :aria-labelledby="`Banner${index}`"
            :href="hero.linkUrl ? hero.linkUrl : '#'"
            role="region"
          >
            <img
              class="gamers-banner-grid__image lazyload blur-up"
              :src="hero.imageFile"
              :alt="hero.bannerSub"
            >

            <div class="gamers-banner-grid__paragraph">
              <span class="h5 gamers-banner-grid__desc" v-text="hero.bannerSub" />

              <a
                :id="`Banner${index}`"
                class="text-link gamers-banner-grid__cta"
                :href="hero.linkUrl"
                :tabindex="tabIndex(index + 1)"
              >
                {{ hero.linkText }}
                <span class="gamers-banner-grid__cta-icon">
                  <upRight />
                </span>
              </a>
            </div>
          </a>
        </template>
      </template>
    </div>

    <div class="gamers-banner-grid__button" v-show="loadMore">
      <button
        @click="loadBanners"
        class="button button--secondary"
        v-text="section.settings.button_label"
      />
    </div>
  </div>
</template>

<script>
import upRight from '~icons/direction/arrow/up-right.svg'

/**
 * Vue: Gamers banner grid (gamers-banner-grid)
 * -----------------------------------------------------------------------------
 * Gamers banner for were all gamers page.
 *
 * @param {String} liquid.sectionId - Section's Liquid ID.
 *
 */
export default {

  components: {
    upRight
  },

  computed: {
    loadMore() {
      return this.itemsPerPage <= this.heroes.length
    }
  },

  data() {
    return {
      itemsPerPage: this.section.settings.itemsPerPage,
    }
  },

  name: 'GamersBannerGrid',

  props: {
    section: {
      type: Object,
      default: {},
    },
    heroes: {
      type: Object,
      default: {},
    },
  },

  methods: {
    ariaHidden(index1) {
      return index1 > this.itemsPerPage ? true : false
    },

    grid_item_classes(index1) {
      const classes = [];
      if (index1 > this.itemsPerPage) classes.push('is-hidden');
      if (index1 === 1) {
        if (this.section.settings.columns === 4) classes.push('gamers-banner-grid__item--four-bleed')
        else classes.push('gamers-banner-grid__item--bleed')
      }
      return classes
    },

    loadBanners() {
      this.itemsPerPage = this.itemsPerPage + 8
    },

    tabIndex(index1) {
      return index1 > this.itemsPerPage ? -1 : 0
    },
  },
}
</script>

<style lang="scss">
  @import './gamers-banner-grid';
</style>
